@font-face {
  font-family: 'Visby CF';
  src: url(./styles/Fonts/VisbyCF-Regular.otf);
  font-weight: 100;
}

@font-face {
  font-family: 'Visby CF';
  src: url(./styles/Fonts/VisbyCF-Bold.otf);
  font-weight: 200;
}

@font-face {
  font-family: 'Visby CF';
  src: url(./styles/Fonts/VisbyCF-ExtraBold.otf);
  font-weight: 300;
}

@font-face {
  font-family: 'Visby CF';
  src: url(./styles/Fonts/VisbyCF-Medium.otf);
  font-weight: 150;
}

@font-face {
  font-family: 'Visby CF';
  src: url(./styles/Fonts/VisbyCF-Light.otf);
  font-weight: 50;
}

@font-face {
  font-family: 'Visby CF';
  src: url(./styles/Fonts/VisbyCF-Thin.otf);
  font-weight: 0;
}

@font-face {
  font-family: 'Seriguela';
  src: url(./styles/Fonts/seriguela/Seriguela-Black.otf);
  font-weight: 0;
}

* {
  font-family: 'Visby CF', sans-serif;
  font-style: normal;
}

body {
  margin: 0;
  width: 100%;
  height: 100%;
}

a {
  color: black;
  text-decoration: none;
  display: inline;
}

a:hover {
  text-decoration: underline;
}


.nav-bar {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0rem 9.8rem 0rem 9.8rem;
  justify-content: space-between;
  width: 100%;
  height: 5rem;
  font-size: 0.75rem;
  /* position: fixed;
  z-index: 1; */
}

/* Start - Logo's CSS */
.logo {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.625rem;
}

.logo img {
  height: 2.5rem;
  width: 2.5rem;
  background-color: #8E00FF;
  border-radius: 0.75rem;
}

.logo h1 {
  font-size: 1.5rem;
  font-weight: 900;
}

/* End - Logo's CSS */


/* Start - navbar CSS */
.nav-bar ul {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  text-decoration: none;
  color: #000;
  list-style-type: none;
  width: 15.6rem;
}

.nav-link {
  text-decoration: none;
  color: #000;
  font-weight: 600;
  font-size: 0.75rem;
}

.nav-buttons {
  display: flex;
  gap: 1.25rem;
}

.sign-up-btn,
.login-in-btn {
  padding: 0.625rem 1.56rem 0.625rem 1.56rem;
  border-radius: 1.5rem;
  font-weight: 600;
}

.sign-up-btn:hover,
.login-in-btn:hover {
  cursor: pointer;
}

.sign-up-btn {
  background: transparent;
  border: 1px solid #8E00FF;
}

.login-in-btn {
  border: none;
  background: linear-gradient(99.94deg, #8E00FF 0%, #7B00DD 100%);
  box-shadow: 0rem 0rem 1.25rem rgba(142, 0, 255, 0.5);
}

@media screen and (max-width: 62.5rem) {
  .nav-bar {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0rem 2rem 0rem 2rem;
    justify-content: space-between;
    width: 100%;
    height: 5rem;
    font-size: 0.75rem;
  }
}

/* End - navbar CSS */


/* Start - Login css */
.log-in {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 56.25rem;
  height: 44.313rem;
  box-shadow: 0rem 0rem 50px rgba(0, 0, 0, 0.1);
  border-radius: 48px;
  padding: 2.625rem 0rem 2.625rem 0rem;
  background-color: aqua;
}

.login-container h1 {
  font-weight: 900;
  font-size: 64px;
}

.text-input-container {
  display: flex;
  flex-direction: column;
}

.text-input-container input {
  width: 28rem;
  padding: 0.75rem 1.5rem 0.75rem 1.5rem;
  border-radius: 1.5rem;
  border: none;
  background: rgba(142, 0, 255, 0.05);
}

.text-input-container input:focus {
  outline: none;
  background: rgba(142, 0, 255, 0.05);
}

.submit-btn {
  width: 28rem;
  padding: 0.75rem 1.5rem 0.75rem 1.5rem;
  border-radius: 1.5rem;
  background: #8E00FF;
  color: #ffffff;
  box-shadow: 0rem 0rem 1.25rem rgba(142, 0, 255, 0.25);
  border: none;
}

.firebase-logins {
  display: flex;
  flex-direction: column;
  margin-top: auto;
}

.continue-with {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.line {
  width: 10.938rem;
  height: 0rem;
  border: 1px solid #555555;
}

.firebase-logins-btn {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.firebase-logins-btn button {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0.75rem 1.875rem 0.75rem 1.875rem;
  border-radius: 24px;
  background-color: transparent;
}

/* End - Login css */


/* Start - Homepage CSS */
.home {
  display: flex;
  flex-direction: row;
}

.home-logo {
  background-color: #8E00FF;
}

.menu-bar {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* min-height: 100vh; */
  min-width: 15rem;
  padding-top: 1rem;
  position: sticky;
  left: 0px;
  top: 0px;
  max-height: 100vh;
  border-right: 2px solid rgb(191, 191, 191);
}

.menu-bar-half-1 {
  height: 50vh;
}

.menu-bar-half-2 {
  height: 50vh;
}

.menu-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.5rem;
}

.hamburger {
  display: flex;
  background: transparent;
  border: none;
  outline: none;
  display: inline-block;
  cursor: pointer;
  gap: 0.25rem;
}

.bar-1,
.bar-2,
.bar-3 {
  width: 1rem;
  height: 0.125rem;
  background-color: #000;
  border-radius: 4px;
  margin: 0.125rem 0;
}

.home-container {
  width: 100vw;
  padding-top: 1rem;
}

.modes {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  padding: 0.125rem;
  border-radius: 24px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
  margin: auto 0rem 0.5rem 0rem;
}

.profile {
  /* position: relative; */
  /* top: 120px; */
}

.modes button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 0.3rem;
  width: 4.75rem;
  height: 2rem;
  border-radius: 24px;
  border: none;
}

.light-mode {
  color: #ffffff;
  background-color: #000;
}

.dark-mode {
  color: #555555;
  background-color: #ffffff;

}

.log-out-btn {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 0.25rem;
  margin-bottom: 2rem;
  width: 11rem;
  height: 2.6rem;
  border-radius: 1.5rem;
  border: none;
  color: #000;
  font-weight: 700;
  font-size: 0.75rem;
  cursor: pointer;
}

.menu-bar-link {
  display: flex;
  flex-direction: column;
  margin: 22px;
  align-self: flex-start;
  gap: 10px;
}

.menu-btn {
  display: flex;
  flex-direction: row;
  align-items: center;
  border: none;
  text-decoration: none;
  gap: 0.688rem;
  padding: 8px 24px 8px 12px;
  border-radius: 20px;
  font-weight: 700;
  font-size: 16px;
  color: #000;
}

.menu-btn:hover {
  background-color: #202020;
  color: #ffffff;
  text-decoration: none;
}

/* Login Page CSS */



/* Recommmendation in Home Page */
.Recommend {
  padding-top: 1.75rem;
  display: flex;
  align-items: center;
}

.ans,
.blog {
  font-weight: 300;
  font-size: 0.75rem;
}

.toggle {
  width: 4.5rem;
  height: 2rem;
  margin-right: 0.5rem;
  margin-left: 0.5rem;
  background: linear-gradient(99.09deg, #8E00FF 0%, #EA4335 100%);
  border-radius: 12px;
  ;
}

.toggler {
  position: relative;
  top: 0.23rem;
  left: 2.70rem;
  width: 1.5rem;
  height: 1.5rem;
  background: #FFFFFF;
  box-shadow: 0px 11px 4px rgba(0, 0, 0, 0.01), 0px 6px 4px rgba(0, 0, 0, 0.05), 0px 3px 3px rgba(0, 0, 0, 0.09), 0px 1px 1px rgba(0, 0, 0, 0.1), 0px 0px 0px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.reco {
  padding-right: 1rem;
  margin-left: 1.75rem;
  font-weight: 300;
  font-size: 1.5rem;
}

.priority {
  margin-left: 1.75rem;
  padding-top: 1rem;
  padding-bottom: 3.5rem;
  display: flex;
  height: 2rem;
}

.priority div {
  margin-right: 0.25rem;

}

.priority button {
  background-color: #FFFFFF;
  font-size: 0.75rem;
  font-weight: 200;
  padding: 10px 16px 10px 16px;
  border-radius: 24px;
}

.foru {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 2.5rem;
  color: #FFFFFF;
  border: 2px solid #FFFFFF;
  background: linear-gradient(99.09deg, #8E00FF 0%, #EA4335 100%);
}

.priority-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #000000;
}

.prob {
  border: 2px solid #000000;
}

.news {
  border: 2px solid #000000;
}

@media only screen and (max-width: 717px) {
  .out {
    display: flex;
    justify-items: center;
    justify-content: start;
  }

  .name {
    display: flex;
    flex-wrap: wrap;
    min-width: 220px;
    justify-content: space-around;
  }

  .main {
    display: flex;
    justify-content: center;
    min-width: 300px;
    min-height: 1000px;
  }

  .title p {
    font-family: 'VisbyCFH';
    font-style: bold;
    font-weight: 900;
    font-size: 30px;
    line-height: 60px;
  }

  .prog {
    position: relative;
    overflow: hidden;
    width: 150px;
    height: 35px;
    border-radius: 15px;
    background-color: red;
  }



  .email {
    display: flex;
    padding-left: 15px;
    min-width: 220px;
    justify-content: space-around;
  }

  .email input {
    width: 220px;
  }

  .create-button {
    width: 200px;
  }

  .left {
    width: 20px;
  }

  .separator {
    padding-top: 22px;
    display: flex;
    align-items: center;
  }

  .name div,
  .email div,
  .email {
    padding-bottom: 10px;
  }

}



/* Start - Answer page  */
.answer {
  display: flex;
  flex-direction: row;
  min-height: 100vh;
}

.answer-container {
  min-height: 100vh;
  width: 100vw;
  max-width: calc(100vw - 15rem);
}

.answer-heading {
  font-weight: 300;
  font-size: 24px;
  margin-left: 1.75rem;
}

/* End - Answer page */